:root {
  --red: hsl(0, 100%, 50%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
::after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.flex {
  display: flex;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.flex-col {
  flex-direction: column;
}

input {
  outline: none;
}

.navbar {
  height: 3.75rem;
  display: flex;
  padding: 0.625rem;
}
.navbar__title_container {
  display: flex;
  align-items: center;
  background-color: darkslategrey;
  color: white;
  border-radius: 0.3125rem;
  padding: 0 0.9375rem;
}
.navbar__title_container h3 {
  font-weight: normal;
}

.todo {
  background-color: lightpink;
  border-radius: 0.3125rem;
}